import {Component} from '@angular/core';
import {Events, IonicPage, MenuController, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {ConfigProvider} from "../../providers/config/config";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {AccountServiceProvider} from "../../providers/account-service/account-service";

declare let google;

@IonicPage()
@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html',
})
export class WelcomePage {

  dWelCome;
  welcomeUser = "";
  welcomeMessage = "";
  updateMessage = "";
  menuForHomePage: any = [];
  accountServicesMenu: any = [];
  borderStyle = "";
  privMenus;
  welcomeError;
  totalBalance;
  accountSummary = [];
  chartType;
  isShowChart;
  chartHeight;
  plusMinusIcon;
  toggle;


  constructor(public  navCtrl: NavController,
              public navParams: NavParams,
              public storage: LocalStorageProvider,
              public config: ConfigProvider,
              public maskTokenUtil: MaskTokenProvider,
              public tokenService: TokenServiceProvider,
              public  cryptoUtil: CryptoParamUtilProvider,
              public menu: MenuController,
              public accountService: AccountServiceProvider,
              public events: Events,
              public gaProvider: GoogleAnalyticsProvider) {

    this.isShowChart = true;
    this.chartHeight = "block";
    this.plusMinusIcon = "md-arrow-dropup-circle";

    this.menu.enable(true);
    //it is disabled in session expire page
    // this.menu.swipeEnable(true);

    this.events.publish('mainMenu', this.initializeMenu());


    let welcomeVal = this.storage.retrieveFromStorage('welcomeVal');
    if (welcomeVal == null || welcomeVal == "") {
      console.log("Welcome text value is null.");
    }
    else {

      this.dWelCome = this.cryptoUtil.getDecryptVal(welcomeVal);
      this.dWelCome = JSON.parse(this.dWelCome);
      this.welcomeUser = this.dWelCome.fName + " " + this.dWelCome.lName;
      this.welcomeMessage = this.dWelCome.welcomeText;


      let appVersion = this.config.iSmartVersion;
      let play_version = this.dWelCome.currentAppVersion;

      appVersion = appVersion.replace(/[.]/g, "");
      play_version = play_version.replace(/[.]/g, "");

      if (play_version > appVersion) {
        this.updateMessage = "A new update with version " + this.dWelCome.currentAppVersion + " is available, Please update your App from Google Play Store";
      }

      else {
        this.updateMessage = "";
      }
    }


    let dbValForMenus = this.storage.retrieveFromStorage('privMenus');
    let newHomePageMenu = JSON.parse(this.cryptoUtil.getDecryptVal(dbValForMenus));

    Object.keys(newHomePageMenu).forEach(key => {
      let value = newHomePageMenu[key];
      if (value.parentId == 0) {
        this.menuForHomePage.push(value);
      }
    });
    let settingPage = {
      "menuTxt": "Settings",
      "menuLink": "#iCellular/SettingsPage",
      "imageUrl": "assets/imgs/appImage/setting.jpg"
    };
    this.menuForHomePage.push(settingPage);

    //  Account Services Menu generate here

    Object.keys(newHomePageMenu).forEach(key => {
      let value = newHomePageMenu[key];
      if (value.parentId == 1) {
        this.accountServicesMenu.push(value);
      }
    });


    //Fetching tx summary without  loading dialog, when page load
    this.intAccountSummary(2);
  }

  ShowHideChart = function () {
    if (this.isShowChart == true) {
      this.isShowChart = false;
      this.chartHeight = "none";
      this.plusMinusIcon = "md-arrow-dropdown-circle";
    }

    else {
      this.isShowChart = true;
      this.chartHeight = "block";
      this.plusMinusIcon = "md-arrow-dropup-circle";
    }
  };
  childPageSelectionAction = function (menuItem) {
    let valueAfterRemHash = (menuItem.menuLink.replace("#", ""));
    let valueAfterReplaceSlash = (valueAfterRemHash.replace("iCellular/", ""));
    this.navCtrl.push(valueAfterReplaceSlash);
  };


  initializeMenu = function () {
    let dbVal = this.storage.retrieveFromStorage('privMenus');
    let pMenu = this.cryptoUtil.getDecryptVal(dbVal);

    if (pMenu == "" || pMenu == null) {
      this.privMenus = [];
    }

    else {
      this.privMenus = JSON.parse(pMenu);
    }

    let parents = [];
    Object.keys(this.privMenus).forEach(key => {
      let value = this.privMenus[key];
      if (value.parentId == 0) {
        parents.push(value);
      }
    });
    this.privMenus = parents;
    return JSON.stringify(this.privMenus);
  };

  pageSelectorAction = function (menuItem) {

    let valueAfterRemHash = (menuItem.menuLink.replace("#", ""));
    let valueAfterReplaceSlash = (valueAfterRemHash.replace("iCellular/", ""));
    let allMenus;
    let dbVal = this.storage.retrieveFromStorage('privMenus');
    let pMenu = this.cryptoUtil.getDecryptVal(dbVal);
    if (pMenu == "") {
      allMenus = [];
    }
    else {
      allMenus = JSON.parse(pMenu);
    }

    let child = [];

    Object.keys(allMenus).forEach(key => {
      let value = allMenus[key];
      if (value.parentId == menuItem.menuId) {
        child.push(value);
      }
    });

    this.storage.insertIntoStorage("childDetails", JSON.stringify(child));
    this.storage.insertIntoStorage("menuText", JSON.stringify(menuItem.menuTxt));

    if (child.length <= 0) {
      this.navCtrl.setRoot('WelcomePage', {});
      this.navCtrl.push(valueAfterReplaceSlash, {});
    }

    else {
      this.navCtrl.setRoot('WelcomePage', {});
      this.navCtrl.push(valueAfterReplaceSlash, {});
    }
  };

  getBorderStyle = function (index) {
    if (index % 2 == 0) {
      this.borderStyle = "";
    }
    else {
      this.borderStyle = "";
      // this.borderStyle = "1px solid #e7e7e7";

    }
    return this.borderStyle;
  };

  getRightBorderStyle = function (index) {
    if (index % 3 == 0) {
      this.borderStyle = "";
    }
    else {
      this.borderStyle = "1px solid #e7e7e7";

    }
    return this.borderStyle;
  };

  SwitchChart = function () {
    if (this.toggle) {
      this.chartType = "value";
      this.showChart(this.accountSummary, this.chartType);
    }

    else{
    this.chartType = "percentage";
    this.showChart(this.accountSummary, this.chartType);
  }
};


generateRow = function (data, accountData) {
  Object.keys(accountData).forEach(key => {
    let value = accountData[key];
    data.addRows([
      [value.accNo, value.balance]
    ]);
  });
};

generateChartOption = function (graphType) {
  // Set chart options
  let options = {

    pieSliceText: graphType,
    'title': 'Account wise summary',
    titleTextStyle: {
      fontName: 'Cabin',
      fontSize: 13,
      align: 'center'
    },

    fontSize: 9,
    legend: {
      alignment: 'center', position: 'right', textStyle: {
        fontName: 'Cabin',
        fontSize: 9
      }
    },
    chartArea: {left: '20', top: '25', width: '100%', height: '100%'},
    colors: ['#C608D1', '#007CC3', '#FDBB2F', '#00529B', '#F47A1F', '#8B00FF', '#7AC142', '#377B2B'],
    is3D: true

  };
  return options;

};

showChart = function (accountData, graphType) {
  let data = new google.visualization.DataTable();
  data.addColumn('string', 'Topping');
  data.addColumn('number', 'Slices');
  this.generateRow(data, accountData);
  let optionsVal = {};
  optionsVal = this.generateChartOption(graphType);

  // Instantiate and draw our chart, passing in some options.
  let chart = new google.visualization.PieChart(document.getElementById('chart_div'));
  chart.draw(data, optionsVal);
};


//Fetching tx summary with/without loading dialog based on loading type 1-> with loading, 2->without loading
intAccountSummary = function (loadingType) {
  this.welcomeError = "";
  let appData = this.storage.retrieveFromStorage("appData");
  let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
  this.tokenService.getNewToken(unMaskRefToken)
    .then(data => {
      this.onNewTokenGenComplete(data, loadingType);
    });
};


onNewTokenGenComplete = function (data, loadingType) {

  if (data.status == 0) {
    this.navCtrl.setRoot('ExpirePage');
  }

  else {
    let appData = this.storage.retrieveFromStorage("appData");

    let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);

    //1 means with loading dialog, for refresh
    if (loadingType == 1) {

      this.accountService.getTxSummary(unMaskToken, appData.userId, appData.appId)
        .then(data => {
          this.onViewAccSummaryFetchComplete(data, loadingType);
        });
    }
    //2 means without loading dialog, for page load
    else {
      this.accountService.getTxSummaryWithoutLoading(unMaskToken, appData.userId, appData.appId)
        .then(data => {
          this.onViewAccSummaryFetchComplete(data, loadingType);
        });
    }
  }


};

onViewAccSummaryFetchComplete = function (data, loadingType) {

  if (data.statusCode == 1) {
    this.accountSummary = data.object.accSummaries;
    this.totalBalance = data.object.totalBalance;

    //1 means with loading dialog, for refresh
    if (loadingType == 1) {
      this.showChart(this.accountSummary, this.chartType);
    }
    else {
      this.chartType = "percentage";
      this.showChart(this.accountSummary, this.chartType);
    }
  }

  else if (data.statusCode == 0) {
    this.welcomeError = data.note;
  }

  else {
    this.welcomeError = "System Error Contact with Administrator.";
  }
};


ionViewDidLoad()
{
  this.gaProvider.startTrackerWithId("IBBL iSmart Home.");
}

}



